.main-about {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 20px;
	gap: 2rem;

	.dropheader {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 1rem;
		color: white;
		font-family: $font-1;
		font-weight: 400;
		font-size: 24px;
		border-radius: 10px;

		img {
			height: 16px;
		}
	}
	.dropmenu-expanded {
		background-color: #f6f6f6;
		padding: 1rem;
		color: $color-1;
		font-family: $font-1;
		font-size: 24px;
		border-radius: 10px;
	}
}
